import React, { PropTypes } from 'react';import Hero from './hero/Hero';import OffCanvas from './common/menu/OffCanvas';const App = ({ children }) => {    return (        <div id="outer-container" className="container">            <Hero>{children}</Hero>			<OffCanvas />			{children}        </div>	);};App.propTypes = { children: PropTypes.object };export default App;


